.footer {
  margin: 67px 0 0 250px;
  padding-bottom: 60px;
}

@media screen and (max-width: 1024px) {
  .footer {
    max-width: 580px;
    margin: 67px auto 0 auto;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    max-width: 282px;
    margin: 48px auto 0 auto;
  }
}
